import { useState } from 'react';
import Reader from './Reader';

// Hook
function useLocalStorage(key, initialValue) {
  // State to store our value
  // Pass initial state function to useState so logic is only executed once
  const [storedValue, setStoredValue] = useState(() => {
    if (typeof window === "undefined") {
      return initialValue;
    }
    try {
      // Get from local storage by key
      const item = window.localStorage.getItem(key);
      // Parse stored json or if none return initialValue
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      // If error also return initialValue
      console.log(error);
      return initialValue;
    }
  });
  // Return a wrapped version of useState's setter function that ...
  // ... persists the new value to localStorage.
  const setValue = (value) => {
    try {
      // Allow value to be a function so we have same API as useState
      const valueToStore =
        value instanceof Function ? value(storedValue) : value;
      // Save state
      setStoredValue(valueToStore);
      // Save to local storage
      if (typeof window !== "undefined") {
        window.localStorage.setItem(key, JSON.stringify(valueToStore));
      }
    } catch (error) {
      // A more advanced implementation would handle the error case
      console.log(error);
    }
  };
  return [storedValue, setValue];
}

function Library(props) {
  const [book1, setBook1] = useLocalStorage("book1", []);
  const [book2, setBook2] = useLocalStorage("book2", []);

  const [reading, setReading] = useLocalStorage("reading", false);

  return (
    <div>
      {!reading ?
        <div>
          <p>Instructions:</p>
          <p>Place your unzipped ebooks in this directory. Set the fields below to the content.opf files inside the ebooks.</p>
          <p>Book 1 (foreign):</p>
          /<input value={book1} onChange={e => setBook1(e.target.value)} placeholder="foreign/path/to/content.opf" />
          <p>Book 2 (english):</p>
          /<input value={book2} onChange={e => setBook2(e.target.value)} placeholder="english/path/to/content.opf" />
          <br />
          <br />
          <button disabled={!book1 || !book2} onClick={() => setReading(true)}>Read</button>
        </div>
      :
        <Reader url1={book1} url2={book2} category="latin" setReading={setReading} />
      }
    </div>
  )
}

export default Library;